
import { computed, defineComponent } from 'vue';
import Card from '../Card.vue';
import AboutMeInfo from '../AboutMeInfo.vue';

export default defineComponent({
  props: {
    info: null,
  },
  components: {
    Card,
    AboutMeInfo,
  },
  setup(props) {
    const src = computed(
      () => process.env.VUE_APP_IMAGE_LOAD_BASE + props.info.about,
    );
    return { src };
  },
});
